import React from "react";
import { Link } from "react-router-dom";
import {
  formatDate,
  localStorageencryp,
} from "../../../components/helpers/function.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import { postDataNoTImeOut } from "../../../config/axios.jsx";
import HeadFormMember from "./HeadFormMember.jsx";
import * as moment from "moment-timezone";

class FormLaporanMember extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      export: false,
      datamember: [],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
    };
    postDataNoTImeOut("member/get/by-tanggal", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
          this.setState({
            export: false,
            datamember: [],
            isLoading: false,
          });
        } else {
          ToastNotification("success", "Laporan Data Member Tersedia");

          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            export: true,
            datamember: res.data,

            isLoading: false,
          });
        }
      })
      .catch((err) => {
        ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
        this.setState({
          export: false,
          datamember: [],
          isLoading: false,
        });
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Member</li>
        </ol>
        <h1 className="page-header">Laporan Member </h1>
        <Panel>
          <PanelHeader>Laporan Member</PanelHeader>
          <br />
          {/* Laporan Member */}
          <div className="container">
            <div className="row">
              <div className="col-12">
                <HeadFormMember
                  isLoading={this.state.isLoading}
                  export={this.state.export}
                  datamember={this.state.datamember}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
            </div>
          </div>
          <br />
          {/* End Tambah Laporan Member  */}
        </Panel>
      </div>
    );
  }
}

export default FormLaporanMember;
