import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { getUserdata, localStoragedecryp } from "../../../../components/helpers/function";

class LaporanDataMemberExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Daftar Member"
          sheet="Laporan Daftar Member"
          buttonText="Export Exel"
        />

        <table
          rowkey="kode_member"
          id="table-to-xls"
          style={{ display: "none" }}
        >
          <thead>
            <tr>
              <th colSpan="9"> Laporan Daftar Member </th>
            </tr>
            <tr>
              <th colSpan="9"> Periode </th>
            </tr>
            <tr>
              <th colSpan="9">
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    "s/d" +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="9"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="9"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
            <tr>
              <th colSpan="9"></th>
            </tr>
            <tr>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                Kode Member
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                Nama Member
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                No Hp
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                Alamat Customer
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                Kecamatan
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                Kelurahan
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                Kota
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                Point
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                Tanggal Lahir
              </th>
            </tr>
          </thead>

          {this.props.data.map((item, index) => (
            <>
              <tbody>
                <tr>
                  <th
                    colSpan="9"
                    style={{ textAlign: "left" }}
                  >
                    {" "}
                    Tanggal Daftar : {item._id.tgl_daftar}{" "}
                  </th>
                </tr>

                {item.detail.map((row, index) => (
                  <>
                    <tr>
                      <td> {row.kode_member}</td>
                      <td> {row.nama_customer}</td>
                      <td> {row.no_hp}</td>
                      <td> {row.alamat_customer}</td>
                      <td> {row.kecamatan}</td>
                      <td> {row.kelurahan}</td>
                      <td> {row.kota}</td>
                      <td> {row.point}</td>
                      <td> {row.tgl_lahir}</td>
                    </tr>
                  </>
                ))}
              </tbody>
              <tfoot>
                <tr>
                  <th
                    style={{
                      backgroundColor: "#E8E5E5",
                      color: "#000",
                      textAlign: "right",
                    }}
                    colSpan="8"
                  >
                    {" "}
                    Jumlah Daftar{" "}
                  </th>
                  <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                    {item.detail.length}
                  </th>
                </tr>
              </tfoot>
            </>
          ))}
          <tr>
            <th
              style={{
                backgroundColor: "#E8E5E5",
                color: "#000",
                textAlign: "right",
              }}
              colSpan="8"
            >
              {" "}
              Total Pendaftar{" "}
            </th>
            <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
              {this.props.data
                .map((list) => list.detail.length)
                .reduce((a, b) => a + b, 0)}
            </th>
          </tr>
        </table>
      </>
    );
  }
}
export default LaporanDataMemberExel;
